import { Component } from '@angular/core';
import { Router, RouterModule } from '@angular/router';

@Component({
  selector: 'app-user-not-approved-layout',
  standalone: true,
  imports: [RouterModule],
  templateUrl: './user-not-approved-layout.component.html',
  styleUrl: './user-not-approved-layout.component.scss'
})
export class UserNotApprovedLayoutComponent {
      constructor(private router: Router) {}

  logout(): void {
    // Clear everything from localStorage
    localStorage.removeItem('authToken');
    localStorage.removeItem('userEmail');
    localStorage.removeItem('userRole');
    localStorage.removeItem('userStatus');
    localStorage.clear(); // optional → clears all keys

    // ✅ Redirect to homepage (Angular route or external site)
    this.router.navigate(['']); 
    // Or if your homepage is an external site:
    // window.location.href = 'https://yourwebsite.com';
  }
} 
